.sponzori
{
    background-color: white;
}
.sponzori_title
{
    color: #055ca9;
    margin-top: 28.5%;
    background-color: white;
    padding-top: 20%;
    font-size: 1.5vw;
    padding-left: 5%;
}
.blueLine{
    width: 93%;
    margin: auto;
    background-color: #055ca9;
    color: #055ca9;
    border:2px solid #055ca9;
    border-radius: 10em;
    margin-top: 1%;
}
.sponzor
{
    display: inline-block;
    height: 7vw;
    background-color: white;
    margin-left: 12vw;
    margin-top: 5vw;
    margin-bottom: 5%;
}
/*Kontakt*/
.kontakty
{
    background-color: #bed1e0;
}
.kontakty_title
{
    color: #055ca9;
    background-color: #bed1e0;
    padding-top: 1.5%;
    text-align: right;
    font-size: 1.5vw;
    padding-right: 5%;
}
.gym_title
{
    color: #055ca9;
    background-color: #bed1e0;
    padding-top: 2%;
    text-align: right;
    font-size: 2vw;
    margin-bottom: 2%;
    margin-right: 15vw;
}
.oddil
{
    margin-top: 1vw;
    background-color: #bed1e0;
    display: block;
    margin-left: 50vw;
}
.oddil p
{
    background-color: #bed1e0;
    color: #055ca9;
    text-align: center;
    font-size: 1vw;
}
.oddil hr
{
    width: 30%;
    margin-left: 17vw;
    color: #055ca9;
    border:0.01vw solid #055ca9;
    border-radius: 10em;
    margin-top: 1%;
}
.social
{
    background-color: #bed1e0;
    border-radius: 0.5em;
    height: 3vw;
    display: inline-block;
    margin-left: 5vw;
    transform: translate(64vw,0);
    margin-top: 1.5vw;
    margin-bottom: 1.5vw;
    cursor: pointer;
}

@media screen and (max-width: 960px) {

    .sponzori
    {
        background-color: white;
    }
    .sponzori_title
    {
        color: #055ca9;
        margin-top: 60%;
        background-color: white;
        padding-top: 20%;
        font-size: 5vw;
        padding-left: 5%;
    }
    .blueLine{
        width: 93%;
        margin: auto;
        background-color: #ffffff;
        color: #055ca9;
        border:2px solid #055ca9;
        border-radius: 10em;
        margin-top: 1%;
    }
    .sponzor
    {
        display: inline-block;
        height: 15vw;
        background-color: white;
        margin-left: 4.7vw;
        margin-top: 10vw;
        margin-bottom: 10%;
    }
    /*Kontakt*/
    .kontakty
    {
        background-color: #bed1e0;
    }
    .kontakty_title
    {
        color: #055ca9;
        background-color: #bed1e0;
        padding-top: 5%;
        text-align: right;
        font-size: 5vw;
        padding-right: 5%;
    }
    .gym_title
    {
        color: #055ca9;
        background-color: #bed1e0;
        padding-top: 7%;
        text-align: center;
        font-size: 4vw;
        margin-bottom: 5%;
        margin-right: 0;
    }
    .oddil
    {
        margin-top: 3vw;
        background-color: #bed1e0;
        display: block;
        margin-left: 0;
    }
    .oddil p
    {
        background-color: #bed1e0;
        color: #055ca9;
        text-align: center;
        font-size: 2.5vw;
    }
    .oddil hr
    {
        width: 35%;
        margin: auto;
        color: #055ca9;
        border:0.01vw solid #055ca9;
        border-radius: 10em;
        margin-top: 1%;
    }
    .social
    {
        background-color: #bed1e0;
        border-radius: 0.5em;
        height: 10vw;
        display: inline-block;
        margin-left: 10vw;
        transform: translate(24.5vw,0);
        margin-top: 5vw;
        margin-bottom: 5vw;
        cursor: pointer;


    }


}
