
input {
    outline: none;
}
input[type=search] {
   -webkit-appearance: textfield;
    -webkit-box-sizing: content-box;
    font-family: inherit;
    font-size: 100%;
}
input::-webkit-search-decoration,
input::-webkit-search-cancel-button {
    display: none;
}


input[type=search] {
    background: url('searchIcon2.png') no-repeat 9px center;

    border: none;
   padding: 5px 10px 5px 32px;
    width: 25px;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    transition: all .5s;
}


input:-moz-placeholder {
    color: #000000;
    left: 100px;
    position: relative;
}
input::-webkit-input-placeholder {
    color: #000;
}

#searcher input[type=search] {
   /* width: 15px;*/
   /* padding-left: 10px;*/
    color: transparent;
    cursor: pointer;
}




#searcher input:-moz-placeholder {
    color: transparent;
}
#searcher input::-webkit-input-placeholder {
    color: transparent;
}

@media screen and (max-width: 960px) {


    input[type=search] {
        background-size: 20px;
    }

}
