.header
{
    color: white;
    margin: auto;
    width: 80%;
    position: relative;
    overflow-x: hidden;

}
.title
{
    margin: auto;
    width: 100%;
    text-align: center;
    font-size: 350%;
    padding-top:0;

}
.subTitle
{

    margin: auto;
    width: 100%;
    text-align: center;
    font-size: 250%;
    padding-top: 3%;
    padding-bottom: 4%;
}
.titleImage
{
    width: 100vw;
    height: auto;

    overflow-x: hidden;
}
.titleFoot
{
    width: 100%;
    margin: auto;
    height: 2.5vh;
    background-color: #1370c2;
    transform: translate(0,-100%);

}


@media screen and (max-width: 960px) {
    .title
    {
        margin: auto;
        width: 100%;
        text-align: center;
        font-size: 7vw;
        padding-top: 2%;

    }
    .subTitle
    {

        margin: auto;
        width: 100%;
        text-align: center;
        font-size: 5vw;
        padding-top: 3%;
        padding-bottom: 3%;

    }
}
