.panel
{
    background-color: #63c2ec;
}
.panel_nadpis
{
    color: white;
    background-color: #63c2ec;
    font-size: 250%;
    padding-top: 2%;
    padding-left: 5%;
}
.whiteLine
{
    width: 93%;
    margin: auto;
    background-color: #ffffff;
    color: white;
    border:2px solid white;
    border-radius: 10em;
    margin-top: 1%;
}
.aktualityContainer
{
    background-color: #63c2ec;
    position: absolute;
    width: 100vw;
    left: 0%;
}
.aktualita
{
    width: 30vw;
    height: 20vw;
    background-color: white;
    margin-top: 7vw;
    margin-left: 2%;
    display: inline-block;
    border-radius: 1em;
    position: relative;;
    margin-bottom: 10%;
    overflow: hidden;
    transition: 0.5s all;

    -webkit-box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
    -moz-box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
    box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
}
.nazevAktuality
{
    background-color: white;
    font-size: 2vw;
    padding-left: 5%;
    padding-top: 5%;
}
.textAktuality
{
    background-color: white;
    font-size: 1.52vw;
    padding-left: 5%;
    text-align: left;
    padding-top: 5%;
    color: #6b6b6b;
}
.leftArrow
{
   display: none;

}
.rightArrow
{
    display: none;
}
#akt1{transform: translate(0,0)}
#akt2{transform: translate(0,0)}
#akt3{transform: translate(0,0)}


.aktualityBtn
{
    width: 15%;
    height:4vw;
    font-size: 2vw;
    cursor: pointer;
    margin-top: 0%;
    border-radius: 10em;
    color: white;
    background-color: #055ca9;
    margin-left: 42%;
    margin-bottom: 5vw;

    box-shadow: 10px 2px 30px 1px rgba(0,0,0,0.2);
}
.aktualityBtn p
{
    padding-top: 1vh;
    background-color: transparent;
    padding-bottom: 1vh;
    margin: auto;
    text-align: center;
}

/*------------------------------------------------------------------------------------*/

@media screen and (max-width: 960px) {
    .panel_nadpis
    {
        color: white;
        background-color: #63c2ec;
        font-size: 5vw;
        padding-top: 2%;
        padding-left: 5%;
    }
    .whiteLine
    {
        width: 93%;
        margin: auto;
        background-color: #63c2ec;
        color: white;
        border:2px solid white;
        border-radius: 10em;
        margin-top: 1%;
    }
    .aktualityContainer
    {
        background-color: #63c2ec;
        white-space: pre;
        position: absolute;
        width: 100vw;
        left: 0%;
        overflow: hidden;
    }
    .aktualita
    {
        width: 50vw;
        height: 30vw;
        background-color: white;
        margin-top: 10%;
        margin-left: 25%;
        display: inline-block;
        border-radius: 1em;
        position: relative;;
        margin-bottom: 10%;
        overflow: hidden;
        transition: 0.5s all;

        -webkit-box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
        -moz-box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
        box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
    }
    .nazevAktuality
    {
        background-color: white;
        font-size: 3.5vw;
        padding-left: 5%;
        padding-top: 5%;
    }
    .textAktuality
    {
        background-color: white;
        font-size: 2.5vw;
        padding-left: 5%;
        text-align: left;
        padding-top: 5%;
        color: #6b6b6b;
    }
    .leftArrow
    {
        position: absolute;
        background-color: transparent;
        width: 10%;
        left: 5%;
        transform: translate(-20%,85%) rotateZ(180deg);
        z-index: 99;
        cursor: pointer;

    }
    .rightArrow
    {
        position: absolute;
        background-color: transparent;
        width: 10%;
        right: 5%;
        transform: translate(20%,85%) ;
        z-index: 99;
        cursor: pointer;

    }
    #akt1{transform: translate(-0%,0)}
    #akt2{transform: translate(-0%,0)}
    #akt3{transform: translate(-0%,0)}
    #leftArrow{display: none}
    #rightArrow{display: unset}

    .aktualityBtn
    {
        width: 30%;
        height: 3%;
        font-size: 4vw;
        cursor: pointer;
        margin-top: 3%;
        border-radius: 25px;
        color: white;
        background-color: #055ca9;
        margin-left: 35%;
        margin-bottom:5%;


        box-shadow: 10px 2px 30px 1px rgba(0,0,0,0.2);
    }
    .aktualityBtn p
    {
        padding-top: 1vh;
        background-color: transparent;
        padding-bottom: 1vh;
        margin: auto;
        text-align: center;
    }




}

