.duvodyClass
{
    width: 100%;
    height: 100vh;
    background-color: white;
    color: #055ca9;
}
.duvodTitle
{
    position: relative;
    margin: auto;
    text-align: center;
    top: 5%;
    background-color: white;
    font-size: 2vw;
}
.duvod{
    display: inline-block;
    color: #ffffff;
    width: 15vw;
    height: 25vw;
    background-color: #e2edfd;
    position: relative;
    margin-top: 10%;
    margin-left: 8vw;
    border: 1px solid #e2edfd;
    border-radius: 1em;

    -webkit-box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
    -moz-box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
    box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);

}
.duvodNumber
{
    color: white;
    background-color: #63c2ec;
    margin: auto;
    margin-top: 15%;
    text-align: center;
    padding: 1px;
    width: 27%;
    height:16%;
    font-size: 3vw;
    border-radius: 500em;
}
.duvodCardTitle
{
    color: #055ca9;
    background-color: #e2edfd;
    margin: auto;
    text-align: center;
    margin-top: 10px;
    font-size: 2vw;
}
.duvodCardText
{
    color: #055ca9;
    background-color: #e2edfd;
    margin: auto;
    text-align: center;
    margin-top: 10px;
    font-size: 1.5vw;
}
.ikona
{
    background-color: #e2edfd;
    height: 15%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 15%;
    margin-top: 15%;
}

#studentBtn
{
    width: 30%;
    height: 10%;
    font-size: 2vw;
    cursor: pointer;
    margin-top: 5%;
    border-radius: 50px;
}
#studentBtn p
{
    padding-top: 1vh;
    background-color: transparent;
    padding-bottom: 1vh;
}
#studentBtnContainer
{
    background-color: white;
}
@media screen and (max-width: 960px) {
    .duvodTitle
    {
        position: relative;
        margin: auto;
        text-align: center;
        /*top: 2%;*/
        background-color: white;
        font-size: 4vw;
        padding-top: 10%;
    }
    .duvod{
        display: inline-block;
        color: #ffffff;
        width: 30vw;
        height: 45vw;
        background-color: #e2edfd;
        position: relative;
        margin-top: 50px;
        margin-left: 13vw;
        border: 1px solid #e2edfd;
        border-radius: 1em;

        -webkit-box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
        -moz-box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);
        box-shadow: 10px 10px 57px 1px rgba(0,0,0,0.2);

    }
    .duvodNumber
    {
        color: white;
        background-color: #63c2ec;
        margin: auto;
        margin-top: 13%;
        text-align: center;
        padding: 1px;
        width: 22%;
        height:14%;
        font-size: 5vw;
        border-radius: 600em;
    }
    .duvodCardTitle
    {
        color: #055ca9;
        background-color: #e2edfd;
        margin: auto;
        text-align: center;
        margin-top: 10px;
        font-size: 4vw;
    }
    .duvodCardText
    {
        color: #055ca9;
        background-color: #e2edfd;
        margin: auto;
        text-align: center;
        margin-top: 10px;
        font-size: 3vw;
    }
    .ikona
    {
        background-color: #e2edfd;
        height: 15%;
        display: block;
        margin-left: auto;
        margin-right: auto;
        height: 15%;
        margin-top: 15%;
    }

    #studentBtn
    {
        width: 50%;
        height: 5%;
        font-size: 4vw;
        cursor: pointer;
        margin-top: 15%;
        border-radius: 50px;

    }
    #studentBtn p
    {
       padding-top: 1vh;
        background-color: transparent;
        padding-bottom: 1vh;
    }
    .duvodyClass
    {
        width: 100%;
        height: 100%;
        background-color: white;
        color: #055ca9;
    }
    #studentBtnContainer
    {
        background-color: white;
        padding-bottom: 15%;
    }

}