.navbar {
    background: linear-gradient(90deg, rgb(19, 112, 194) 0%, rgb(19, 112, 194) 100%);
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: relative;
    top: 0;
    z-index: 999;
}

.navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 120px;
    max-width: 1500px;
}

.navbar-logo {
    position: absolute;
    top: 25px;
    left: 70px;
}

.navbar-logo img{
    width: 80px;
}




.fa-typo3 {
    margin-left: 0.5rem;
    font-size: 1.8rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
    z-index: 999999999;
}

.nav-item {
    height: 80px;
}

.nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.nav-links:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
}

.fa-bars {
    color: #fff;
}

.nav-links-mobile {
    display: none;
}

.hamburger {
    display: none;
}
#searcher {
    top: 35%;
    right: 3vw;
    position: absolute;
}
#searcher input[type=search]:focus{
    width: 120px;
    color: #ffffff;
    background-color: #1370c2;
    cursor: auto;
    border: 5px solid #ffffff;
    border-radius: 10em;
    padding-left: 10px;
    background: #1370c2;
}
@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 120px;
        left: -100%;
        opacity: 90;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #1370c2;
        left: 0;
        opacity: 90;
        transition: all 0.5s ease;
        z-index: 999999;
    }

    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        z-index: 9999999999;
    }

    .nav-links:hover {
        background-color: #fff;
        color: #242424;
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 10px;
        left: 20px;

    }
    .navbar-logo img{
        width: 50px;

    }

    .hamburger {
        display: block;
        position: relative;
        right: -20vw;
        font-size: 1.8rem;
        cursor: pointer;
        transform: translate(0, -145%);

    }
    #searcher {
         top: -20%;
        right: -25vw;
        position: relative;
    }
    #searcher input[type=search]:focus{
        width: 90px;
        color: #ffffff;
        background-color: #1370c2;
        cursor: auto;
        border: 5px solid #ffffff;
        border-radius: 10em;
        padding-left: 10px;
        background: #1370c2;
    }


    .nav-links-mobile {
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: #fff;
        padding: 14px 20px;
        border: 1px solid #fff;
        transition: all 0.3s ease-out;
    }

    .nav-links-mobile:hover {
        background: #fff;
        color: #242424;
        transition: 250ms;
    }
}

@media screen and (max-width: 400px) {
    #searcher input[type=search]:focus{
        width: 70px;
        color: #ffffff;
        background-color: #1370c2;
        cursor: auto;
        border: 5px solid #ffffff;
        border-radius: 10em;
        padding-left: 10px;
        background: #1370c2;
    }
}